import styled from "styled-components";
import imagem from "../image.png"
import github from '../download.png'

const FooterContainer = styled.footer`
    align-items:center;
    display: flex;
    height: 500px;
    gap: 40px;
    justify-content: center;
    width: 100%;

    .footer__titulo {
        text-align:center;
    }
    
    .footer__imagem {
        max-height: 79px;
        max-width: 87px;
    }
    `
    
const Framework = styled.div`
    align-items:center;
    justify-content:center;
    display: flex;
    gap: 12px;
    
`
const Anchor = styled.a`
    align-items:center;
    justify-content:center;
    display: flex;
    gap: 12px;
`

function Footer() {
    return (
        <FooterContainer>
            <Framework>
                <h3 className="footer__titulo">Powered By</h3>
                <img src={ imagem } className="footer__imagem" alt="Powered By React JS" />
            </Framework>
            <Anchor>
                <a href="https://github.com/A-Abraao/calculadora/tree/main"><img src={ github } className="footer__imagem"/></a>
            </Anchor>

        </FooterContainer>
    )
}

export default Footer